/* Microsoft-style Theme */
:root {
  --primary-blue: #0067b8;
  --darker-blue: #005397;
  --black: #000000;
  --white: #ffffff;
  --gray-100: #f5f5f5;
  --gray-200: #e6e6e6;
  --gray-300: #c7c7c7;
  --gray-400: #959595;
  --gray-500: #767676;
  --font-segoe: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
}

body {
  font-family: var(--font-segoe);
  background-color: var(--white);
  color: var(--black);
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

/* Layout */
.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header & Navigation */
.header {
  background-color: var(--white);
  box-shadow: 0 1px 0 rgba(0,0,0,0.1);
}

.nav-menu {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 5%;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: transparent;
}

.nav-links {
  display: flex;
  gap: 24px;
}

.nav-item {
  color: var(--black);
  font-size: 13px;
  padding: 16px 8px;
  text-transform: none;
  position: relative;
}

.nav-item:hover::after {
  content: '';
  position: absolute;
  bottom: 12px;
  left: 8px;
  right: 8px;
  height: 2px;
  background-color: var(--black);
}

.nav-item.active::after {
  content: '';
  position: absolute;
  bottom: 12px;
  left: 8px;
  right: 8px;
  height: 2px;
  background-color: var(--black);
}

/* Main Content */
.main {
  flex: 1;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 5%;
  width: 100%;
}

/* Blog Posts Grid */
.blog-posts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin: 48px 0;
}

/* Post Card */
.post {
  background: var(--white);
  border: none;
  text-align: left;
  padding: 0;
  margin-bottom: 24px;
  transition: transform 0.2s;
  cursor: pointer;
}

.post:hover {
  transform: translateY(-4px);
}

.post-image {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  margin-bottom: 16px;
}

.post h2 {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0 0 8px 0;
  color: var(--black);
}

.post-meta {
  color: var(--gray-500);
  font-size: 0.875rem;
  margin-bottom: 12px;
}

.post-content {
  font-size: 1rem;
  color: var(--gray-500);
  margin-bottom: 16px;
}

/* Category Tags */
.category-tag {
  background: var(--gray-100);
  color: var(--black);
  padding: 4px 12px;
  border-radius: 2px;
  font-size: 0.75rem;
  margin-right: 8px;
  display: inline-block;
  text-transform: uppercase;
}

/* Buttons */
.primary-button {
  background: var(--primary-blue);
  color: var(--white);
  padding: 8px 16px;
  border: none;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.primary-button:hover {
  background: var(--darker-blue);
}

.secondary-button {
  background: transparent;
  color: var(--primary-blue);
  padding: 8px 16px;
  border: 1px solid var(--primary-blue);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s;
}

.secondary-button:hover {
  background: rgba(0,103,184,0.1);
}

/* Search Bar */
.search-bar {
  max-width: 600px;
  margin: 24px auto;
}

.search-bar input {
  width: 100%;
  padding: 12px;
  border: 2px solid var(--gray-200);
  border-radius: 2px;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.search-bar input:focus {
  border-color: var(--primary-blue);
  outline: none;
}

/* Footer */
.footer {
  background: var(--gray-100);
  padding: 48px 0;
  margin-top: 48px;
}

.footer-content {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 5%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
  text-align: left;
}

.footer h3 {
  font-size: 1rem;
  margin-bottom: 16px;
  color: var(--black);
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 12px;
}

.footer-links a {
  color: var(--gray-500);
  font-size: 0.875rem;
  text-decoration: none;
}

.footer-links a:hover {
  color: var(--primary-blue);
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .blog-posts {
    grid-template-columns: 1fr;
  }
  
  .nav-menu {
    height: auto;
    padding: 8px 5%;
    flex-direction: column;
  }
  
  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }
}